.recipe{
border-radius: 10px;
margin: 20px;
display: flex;
flex-direction: column;
justify-content: space-around;
background: rgba(255, 255, 255, 0.788);
align-items: center;
min-width: 40%;
}
.recipe:hover{
    box-shadow: 3px 3px 3px gray;
}
.image{
    border-radius: 50%;
    width: 150px;
    height: 150px;
    margin-bottom: 20px;
}
.calories{
    font-size: 1.3rem;
    
}
.ingredients{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width:80%;
    font-size: 1.2rem;
    font-weight: 500;
}
