.App{
  min-height: 100vh;
  background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
}
.search-form{
min-height: 10vh;
display: flex;
justify-content: center;
align-items: center;
}
.search-bar{
  width: 30%;
  border: none;
  padding: 10px;
}
.search-button{
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
  padding: 12px;
  font-weight: 700;
  border-radius: 5%;
}
.search-button:hover{
  background-color: red;
  border-radius: 10%;
  box-shadow: 2px 1px 3px rgb(105, 100, 90);
}
.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
